import s from './ProfileInfo.module.css';
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatusWithHooks from "./ProfileStatus/ProfileStatusWithHooks";
import userPhoto from "./../../../assets/images/user.png";
import {useState} from "react";
import ProfileDataForm from "./ProfileDataForm/ProfileDataForm";

const ProfileInfo = ({profile, status, updateStatus, isOwner, savePhoto, saveProfile}) => {
    let [editMode, setEditMode] = useState(false);
    let [showMoreMode, setShowMoreMode] = useState(false);

    let onSubmit = (formData) => {
        saveProfile(formData).then(
            () => {
                setEditMode(false);
            }
        )
    }

    const onMainPhotoSelected = (e) => {
        if (e.target.files.length)
            savePhoto(e.target.files[0]);
    }

    if (!profile)
        return <Preloader/>

    return <div>
        <div className={s.descriptionBlock}>
            <img src={profile.photos.large || userPhoto}/>
            {!editMode
                ? <ProfileData profile={profile} isOwner={isOwner} showMoreMode={showMoreMode}
                               goToEditMode={() => setEditMode(true)}/>
                : <div>
                    {isOwner && <input type={'file'} onChange={onMainPhotoSelected}/>}
                    <ProfileDataForm initialValues={profile} onSubmit={onSubmit} profile={profile}/>
                </div>}
            <ProfileStatusWithHooks status={status} updateStatus={updateStatus} isOwner={isOwner}/>
            {!editMode && <span className={s.showMoreLessInfo}
                                onClick={() => {
                                    showMoreMode ? setShowMoreMode(false) : setShowMoreMode(true)
                                }}>
                {showMoreMode ? "Show less" : "Show more"}
            </span>}
        </div>
    </div>
}

const ProfileData = ({profile, isOwner, goToEditMode, showMoreMode}) => {
    return <div>
        {isOwner && <button className={s.editModeButton} onClick={goToEditMode}>edit</button>}
        <div>
            <b>Full name</b>: {profile.fullName}
        </div>
        {showMoreMode && <div>
            <div>
                <b>Looking for a job</b>: {profile.lookingForAJob ? "yes" : "no"}
            </div>
            <div>
                <b>Professional skills</b>: {profile.lookingForAJobDescription}
            </div>
        </div>}
        <div>
            <b>About me</b>: {profile.aboutMe}
        </div>
        {showMoreMode && <div>
            <b>Contacts</b>: {Object.keys(profile.contacts).map(key => {
            return <Contact key={key} contactTitle={key} contactValue={profile.contacts[key]}/>
        })}
        </div>}
    </div>
}

const Contact = (
    {
        contactTitle, contactValue
    }
) => {
    return <div>
        <b className={s.contacts}>{contactTitle}</b>: {contactValue}
    </div>
}

export default ProfileInfo;